.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 1200;
  }
  .modal {
    display: flex;
    position: relative;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 75%;
    height: 50%;
    padding: 30px;
    border: 1px solid salmon;
    border-radius: 10px;
    background-color: rgba(0, 0, 0, 0.7);
  }
  
  .controls {
    display: flex;
  }
  
  .close {
    position: absolute;
    right: 10px;
    top: 10px;
    background-color: transparent;
    border: none;
    color: salmon;
    cursor: pointer;
  }
  .closeIcon:hover {
    color: blanchedalmond;
  }